html {
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}

body {
	margin: 0;
	padding: 0;
	background: #FFFFEE;
	font-size: 12px;
	overflow: auto;
}

#main {
	width: 100%;
	height: 100%;
}

#content {
	width: 100%;
}

#loveHeart {
  text-align: center;
}

#elapseClock {
	font-size: 12px;
	margin-top: 10px;
	margin-bottom: 10px;
}

#messages{
	font-family: "sans-serif";
	width: 100%;
	font-size: 16px;
  color:#666;
	width: 100%;
  opacity: 0;
}

#elapseClock .digit{
	font-family: "digit";
	font-size: 26px;
}

.bell {
  width: 2rem;
  -webkit-animation: bellshake 2.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both infinite;
          animation: bellshake 2.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both infinite;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform-origin: top right;
      -ms-transform-origin: top right;
          transform-origin: top right;
}

@-webkit-keyframes bellshake {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  3% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  7% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  11% {
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
  }
  15% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  18% {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
  }
  21% {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
  }
  23% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
  }
  25%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
}

@keyframes bellshake {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  3% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  7% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  11% {
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
  }
  15% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  18% {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
  }
  21% {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
  }
  23% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
  }
  25%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
}

#copyright{
	margin-top: 10px;
	text-align: center;
	width:100%;
	color:#666;
  position: absolute;
  opacity: 0;
}

#errorMsg{
	width: 100%;
	text-align: center;
	font-size: 24px;
	position: absolute;
	top: 100px;
	left:0px;
}

#message{
  margin-top: 30vh;
}

#message p{
  margin: 10px 0;
}


